<template>
	<div id="advertise-order">
		<public-header title="购买记录"></public-header>
		<div class="content">
			<div class="wrap-search wrap-search-input">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.status == -1}" @click="onToggleStatus(-1)">全部</div>
					<div class="tab-item" :class="{active: filter.items.status == 0}" @click="onToggleStatus(0)">未开始</div>
					<div class="tab-item" :class="{active: filter.items.status == 1}" @click="onToggleStatus(1)">进行中</div>
					<div class="tab-item" :class="{active: filter.items.status == 2}" @click="onToggleStatus(2)">已下架</div>
				</div>
				<van-search placeholder="请输入商户名称" v-model="filter.items.keyword" />
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="order-list"
			>
				<div class="order-item" v-for="(item, index) in records.data" :key="item.id">
					<div class="order-title flex-lr">
						<div class="title flex">
							<div class="title-img">
								<img :src="item.logo" alt="">
							</div>
							<div class="title-name">{{item.store_title}}</div>
						</div>
						<div class="itag itag-danger" v-if="item.status == 0">未开始</div>
						<div class="itag itag-primary" v-if="item.status == 1">进行中</div>
						<div class="itag itag-disabled" v-if="item.status == 2">已结束</div>
					</div>
					<div class="order-detail border-1px-t">
						<div class="order-info flex">
							<div>广告类型：</div>
							<div>{{item.title}}</div>
						</div>
						<div class="order-info flex">
							<div>支付费用：</div>
							<div class="c-danger">￥{{item.final_fee}}</div>
						</div>
						<div class="order-info flex">
							<div>购买时间：</div>
							<div>{{item.days}}天</div>
						</div>
						<div class="order-info flex">
							<div>有效期：</div>
							<div>{{item.addtime_cn}} ~ {{item.endtime_cn}}</div>
						</div>
					</div>
				</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>没有符合条件的数据!</p>
			</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				showPreLoading: true,
				isRefresh: false,
				filter: {
					items: {
						status: '-1'
					}
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/advertise/trade/list'
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
			}
		},
		mounted(){
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		}
	}
</script>

<style>
	#advertise-order .content .order-item{
		margin-bottom: 10px;
		background-color: #fff;
	}
	#advertise-order .content .order-item .order-title{
		padding: 10px;
	}
	#advertise-order .content .order-item .order-title .title-img{
		width: 35px;
		height: 35px;
	}
	#advertise-order .content .order-item .order-title .title-img img{
		width: 100%;
		height: 100%;
		border-radius: 100%;
	}
	#advertise-order .content .order-item .order-title .title-name{
		font-size: 16px;
		font-weight: bold;
		padding: 0 10px;
	}
	#advertise-order .content .order-item .order-detail{
		padding: 10px;
	}
	#advertise-order .content .order-item .order-detail .order-info{
		font-size: 14px;
		color: #7a7a7a;
		padding-bottom: 8px;
	}
	#advertise-order .content .order-item .order-detail .order-info:last-child{
		padding-bottom: 0px;
	}
	#advertise-order .content .order-item .btn-group{
		display: flex;
		flex-wrap: nowrap;
		justify-content: flex-end;
		align-items: center;
		width: 100%;
		padding: 10px;
	}
	#advertise-order .content .order-item .btn-group .btn-item{
		min-width: 60px;
		margin-left: 10px;
		text-align: center;
		padding: 10px 8px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		font-size: 14px;
	}
</style>


